
* {
  margin: 0;
  padding: 0;
}
html,
body,
#app {
  width: 100%;
  height: 100%;
}
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app .main {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  overflow: auto;
  position: relative;
}
#app .main img {
  position: absolute;
}
#app .main #pane {
  position: absolute;
  border: 4px solid rgba(255, 235, 59, 0.61);
}
#app .main .circle {
  position: absolute;
  background-color: #fff;
  border: 1px solid lime;
  border-radius: 50%;
  width: 10px;
  height: 10px;
}
#app .side {
  height: 100%;
  width: 260px;
  border-left: solid 2px silver;
}
#app .side > div {
  border-top: solid 2px silver;
}
#app .side ul li {
  list-style: none;
  font-size: .8em;
  cursor: pointer;
}
#app .side ul li.active {
  color: blue;
}
#app .side ul li span {
  float: right;
  margin-right: 1em;
}
#app .side ul li span:hover {
  color: red;
}
#app .side ul li.hint {
  font-style: italic;
  color: #555;
}
#app .side .positions > ul {
  max-height: calc(60vh);
  overflow: auto;
}
#app .side button {
  width: 40%;
  padding: 0.5em;
  margin: 0.5em;
}
#app .side input[type='file'] {
  display: none;
}
#app .side textarea {
  width: 100%;
  min-height: 4em;
}
#app .side h4 {
  font-size: 1rem;
  background-color: silver;
  padding-left: 2px;
}
